<template>
    <div id="home">
        <nav-bar>
            <template v-slot:default>图书商城</template>
        </nav-bar>

        <tab-control  v-show="isTabFixed" @tabClick='tabClick' :titles="['畅销','新书','精选']"></tab-control>

        <div class="wrapper">
            <div class="content">
                <div ref="banref">
                    <div class="banners">
                        <home-swiper :banners="banners"></home-swiper>

                        <recommend-view :recommends = "recommends"></recommend-view>
                        <div class="tabControl" >
                    <tab-control @tabClick='tabClick' :titles="['畅销','新书','精选']"></tab-control>
                </div>
                    </div>
                </div>
                
                <goods-list :goods="showGoods"></goods-list>
            </div>
        </div>
        <back-top @bTop="bTop" v-show="isShowBackTop"></back-top>
    </div>

</template>

<script>
import NavBar from 'components/common/navbar/NavBar.vue';
import RecommendView from './ChildComps/RecommendView.vue';
import {getHomeAllData, getHomeGoods} from 'network/home';
import {ref, onMounted, reactive, computed, watchEffect, nextTick} from 'vue';
import TabControl from '../../components/content/tabControl/TabControl.vue';
import GoodsList from '../../components/content/goods/GoodsList.vue';
import BScroll from 'better-scroll';
import BackTop from '../../components/common/backtop/BackTop.vue';
import HomeSwiper from './ChildComps/HomeSwiper.vue';

export default {
    name:'Home',

    setup() {
        let isTabFixed = ref(false);
        let isShowBackTop = ref(false);
        let banref = ref(null)
        // 声明一个临时变量
        // let temid = ref(0);
        const recommends = ref([])

        // 商品列表数据模型
        const goods = reactive({
            sales:{page:1, list:[]},
            new:{page:1, list:[]},
            recommend:{page:1, list:[]},
        })

        let currentType = ref('sales');

        const showGoods = computed(() => {
            return goods[currentType.value].list
        })

        let bscroll = reactive({});

        let banners = ref([]);
        
        onMounted(() => {
            getHomeAllData().then(res => {
                // console.log(res.goods)
                recommends.value = res.goods.data;
                // console.log(res.slides);
                banners.value = res.slides;
            })

            getHomeGoods('sales').then(res => {
                // console.log(res.goods.data)
                goods.sales.list = res.goods.data;
            })

            getHomeGoods('recommend').then(res => {
                // console.log(res)
                goods.recommend.list = res.goods.data;
            })

            getHomeGoods('new').then(res => {
                // console.log(res)
                goods.new.list = res.goods.data;
            })
            
            //创建BetterScroll对象
            bscroll = new BScroll(document.querySelector('.wrapper'), {
                probeType: 3, //0, 1, 2, 3,  3 只要在运动就触发事件
                click: true, // 是否允许滑动区域内点击
                pullUpLoad: true // 上拉加载更多， 默认为false
            });
                
            // 触发滑动事件
            bscroll.on('scroll', (position) => {
                console.log(banref.value.offsetHeight);
                console.log(-position.y);

            isShowBackTop.value = isTabFixed.value = (-position.y) > banref.value.offsetHeight;
            })

            // 上拉加载数据，触发pullingUp
            bscroll.on('pullingUp', () => {
               console.log('上啦');

                const page = goods[currentType.value].page + 1

                getHomeGoods(currentType.value, page).then(res => {
                    // 这里需要用解构赋值，不能直接push追加数据
                    goods[currentType.value].list.push(...res.goods.data);
                    goods[currentType.value].page += 1
                })

                // 完成上拉后需要等数据加载完，再将新数据展示数据
                bscroll.finishPullUp();

                // 页面加载就需要重新计算高度， 必须使用的
                bscroll.refresh(); 
                console.log(document.querySelector('.content').clientHeight);
                console.log("当前类型" + currentType.value + ",当前页面" + page);
            })
            // console.log(goods);

        })

        const tabClick = (index) => {
            // temid.value = index;

            let types = ['sales', 'new', 'recommend'];

            currentType.value = types[index];

            nextTick(() => {
                // 重新计算高度
                bscroll && bscroll.refresh();
            })
        }

        // 监听任何一个变量有变化时触发
        watchEffect(() => {
            // 延迟执行，等页面、dom都渲染完再更新高度
            nextTick(() => {
                // 重新计算高度
                // 先判断bscroll是否存在后再调用
                bscroll && bscroll.refresh();
            })
        })

        // 定义‘回到顶部’自定义事件的方法
        const bTop = () => {
            // console.log('11');
            // 调用scroll自身的方法 可以指定去到哪个位置scrollTo(x轴, y轴, 过渡毫秒)；
            bscroll.scrollTo(0, 0, 500);
        }

        return {
            recommends,
            // temid,
            tabClick,
            goods,
            showGoods,
            isTabFixed,
            isShowBackTop,
            banref,
            bTop,
            banners,
        }
    },

    components: {
        NavBar,
        RecommendView,
        TabControl,
        GoodsList,
        BackTop,
        HomeSwiper,
    },
}
</script>

<style scoped lang="scss">
.banners img{
    width: 100vw;
    height: auto;
}

#home {
    position: relative;
    height: 100vh;
}

.wrapper {
    position: absolute;
    top: 44px;
    bottom: 50px;
    left: 0;
    right: 0;
    overflow: hidden;

    .content {
        .banners {
            .tabControl {
    position: sticky;
    background: red;
}
        }
    }
}

</style>